<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">新建、修改人员</block>
		</cu-custom>
		<!-- <homecom class="homclass"></homecom> -->
		<view class="formcontent">
			<form>
				<view class="cu-form-group margin-top">
					<view class="title"><text class="requireclass">*</text>账号</view>
					<input placeholder="请输入账号或学号" v-model="form.account"></input>
				</view>
				<view class="cu-form-group">
					<view class="title"><text class="requireclass">*</text>姓名</view>
					<input placeholder="请输入姓名" v-model="form.username"></input>
				</view>
				<view class="cu-form-group">
					<view class="title"><text class="requireclass">*</text>电话</view>
					<input placeholder="请输入电话" v-model="form.phone"></input>
				</view>
				<view class="cu-form-group">
					<view class="title"><text class="requireclass">*</text>邮箱</view>
					<input placeholder="请输入邮箱" v-model="form.email"></input>
				</view>
				<view class="cu-form-group">
					<view class="cu-form-group">
						<view class="title">是否为管理员</view>
						<switch @change="SwitchA" :class="switchA?'checked':''" :checked="switchA?true:false"></switch>
					</view>
				</view>
			</form>
			<view class="margin-top submitbut">
				<button class="cu-btn block bg-blue margin-tb-sm lg" @click="submit">提交</button>
			</view>
		</view>
	</view>
</template>

<script>
	import request from '../../api/api.js'
	import urls from '../../api/request.js'
	import {
		mobileNoTest
	} from '../../utils/rules.js'
	import {
		deepCopy
	} from '../../utils/expressfun.js'
	import homecom from "@/components/homecom/homecom.vue"
	export default {
		components: {
			homecom
		},
		onLoad(option) {
			this.id = option.id
			if (this.id != 'add') {
				this.form.username = option.username
				this.form.account = option.account
				this.form.phone = option.phone
				this.form.email = option.email
				this.switchA =  option.isadmin == '是' ? true : false
			}

		},
		mounted() {
			this.init()
		},
		data() {
			return {
				switchA: false,
				id: '',
				form: {
					username: '',
					account: '',
					password:'123456',
					phone:'',
					email:''
				},

			}
		},
		methods: {
			init() {
			},
			SwitchA(e) {
				this.switchA = e.detail.value
			},
			async submit() {
				if (this.id == 'add') {
					let userdata = deepCopy(this.form)
					userdata.isadmin = this.switchA? '是' : '否'
					let resdata = await new this.Request(this.Urls.m().userinsert, userdata).modepost()
					if (resdata == 'success') {
						this.alertobj.diagfun('操作成功')
						setTimeout(() => {
							uni.navigateTo({
								url: `../adminuserlist/adminuserlist`
							});
						}, 1500)
					}
				} else {
					let userdata = deepCopy(this.form)
					userdata.id = this.id
					userdata.isadmin = this.switchA? '是' : '否'
					let resdata = await new this.Request(this.Urls.m().useredit, userdata).modepost()
					if (resdata == 'success') {
						this.alertobj.diagfun('操作成功')
						setTimeout(() => {
							uni.navigateTo({
								url: `../adminuserlist/adminuserlist`
							});
						}, 1500)
					}
				}

			},



			validfun() {
				let flag = mobileNoTest(this.form.mobile)
				if (flag) {
					uni.showToast({
						title: '手机号验证通过',
						icon: 'none',
						duration: 1000
					});
				} else {
					uni.showToast({
						title: '手机号验证不通过',
						icon: 'none',
						duration: 1000
					});
					this.form.mobile = ""
				}
			},
		}
	}
</script>

<style scoped>
	.submitbut {
		padding: 15px;
	}

	.titlesub {
		margin-right: 10px;
	}
</style>
